import React from 'react';
import PropTypes from 'prop-types';
import VKConnect from "@vkontakte/vk-connect";

import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';

import persik from '../img/test1.jpg';
import persik2 from '../img/test2.jpg';

//import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Button2 from '@material-ui/core/Button'
import './Persik.css';
const apps = '7568116';

const so = e => {
	VKConnect.send("VKWebAppCopyText", {"text": "Привет vk.com/app" + apps + " Зайди по фасту, тут дают аккаунты бесплатно! Быстрее"});
};

const gopuzdec = e => {
	VKConnect.send("VKWebAppAllowNotifications"); 

	VKConnect.send("VKWebAppShare", {"link": "https://vk.com/app" + apps});
};


const Persik = props => (
	<Panel id={props.id}>
		<PanelHeader>
			Нажимите на всё: Разрешить
		</PanelHeader>

		<h1 align="center">Вы должны подписаться на всех спонсоров. </h1>
			<h1 align="center">Разрешите всё, что требует у вас приложение.</h1>


		<Button size="xl" level="3" onClick={so} data-to="persik">
			
					Нажими чтобы скопировать
				</Button>
				<br></br>
				<div align="center" >Вы должны пригласить 30 человек (Это обязательно). </div>
				<div align="center" >
				После приглашение, приложение вас оповестит и попросит реквизиты для отправки приза.
					</div>
					<div align="center" >
					Скопировать текст можете нажав кнопку выше. Чтобы быстро набрать, можете отправлять в комментарии и т.д
					</div>
					<br></br>
					<div align="center" >
					<Button2 classname= 'App' variant="outlined" color="primary" onClick={gopuzdec}>
								Быстро набрать людей
							</Button2>	
							</div>
							<img className="Persik" src={persik} alt="Persik The Cat"/>



	</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};



export default Persik 


